export default {
	data() {
		return {
			globalData: {},
			details:{},
			formData:{
				type:"1",
				amount:'',
				name:'',
				tel:'',
				bank_card:'',
				bank_address:'',
			}
		};
	},
	onLoad() {
		this.globalData = getApp().globalData;
		this.loadData();
	},
	methods: {
		loadData() {
			this.$minApi
				.wallet()
				.then(res => {
					this.details = res.data;
				})
				.catch(err => {
					console.log(err);
				});
		},
		onSubmit(){
			let errMsg = '';
			this.formData.type = parseInt(this.formData.type)
			if(this.formData.type == 2){
				if(!this.formData.bank_card){
					errMsg = "请输入银行卡号"
				}else if(!this.formData.tel){
					errMsg = "请输入银行预留手机号"
				}else if(!this.formData.name){
					errMsg = "请输入银行卡姓名"
				}else if(!this.formData.bank_address){
					errMsg = "请输入开户行"
				}
			}
			if(!errMsg){
				this.formData.amount = this.formData.amount ? parseInt(this.formData.amount) : 0;
				if(this.formData.amount <= 0){
					errMsg = "提现金额必须大于0"
				}else if(this.formData.amount > this.details.balance){
					errMsg = "提现金额大于当前余额"
				}
			}
			if(errMsg){
				uni.showToast({
					icon: 'none',
					title: errMsg,
					duration: 2000
				});
				return false;
			}
			this.$minApi
				.withdrawAdd(this.formData)
				.then(res => {
					uni.showToast({
						title: "提交成功",
						duration: 2000
					});
					setTimeout(function(){
						uni.navigateBack({
						    delta: 1
						});
					},2000)
				})
				.catch(err => {
					console.log(err);
				});
		},
		RadioChange(e) {
			this.formData.type = e.detail.value
		},
		sumAll(){
			this.formData.amount = this.details.balance
		},
		InputAmount(e) {
			this.formData.amount = e.detail.value
		},
		InputName(e) {
			this.formData.name = e.detail.value
		},
		InputTel(e) {
			this.formData.tel = e.detail.value
		},
		InputBankCard(e) {
			this.formData.bank_card = e.detail.value
		},
		InputBankAddress(e) {
			this.formData.bank_address = e.detail.value
		},
	}
};
